.geotour-warning {
    border: 1px solid #f0ad4e; 
    background-color: #fcf8e3; 
    color: #8a6d3b; 
    padding: 10px;
    border-radius: 4px; 

    code {
        background-color: #f7f7f9;
        padding: 2px 4px;
        border-radius: 3px;
        font-family: monospace; 
    }
}


// sass/geotour-information.scss

.pois-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr); 
    gap: 10px;
  
    @media (max-width: 1200px) {
      grid-template-columns: repeat(3, 1fr); 
    }
  
    @media (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr); 
    }
  
    @media (max-width: 768px) {
      grid-template-columns: repeat(1, 1fr); 
    }
  }
  
  .poi-item {
    background: #eee;
    padding: 6px;
  }
  
  .poi-image-wrapper {
    position: relative;
    width: 100%;
    padding-bottom: 56.25%; // 16:9 aspect ratio
    height: 0; 
    overflow: hidden;
  
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  
  .poi-categories {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    padding: 5px;
    font-size: 14px;
  }
  
  .poi-distance {
    position: absolute;
    top: 5px;
    right: 5px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    padding: 3px 8px;
    font-size: 12px;
    border-radius: 3px;
  }